.list-item[data-completed='true'] {
  color: var(--clr-lighter-gray-200);
  text-decoration: line-through;
}

.list-item[data-completed='true'] .complete-btn {
  background-image: var(--clr-primary-gradient);
  border: none;
}

// remove pseudo element on hover
.list-item[data-completed='true'] .complete-btn:hover::after {
  content: none;
}
