*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

/* Reset margins */
body,
h1,
h2,
h3,
h4,
h5,
p,
figure,
picture {
  margin: 0;
}

/* Allow percentage-based heights in the application */
html,
body {
  height: 100%;
}

/* Typographic tweaks! Add accessible line-height Improve text rendering */
body {
  line-height: 1.5;
  -webkit-font-smoothing: antialiased;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  font-weight: 400;
}

/* Avoid text overflows */
p,
h1,
h2,
h3,
h4,
h5,
h6 {
  overflow-wrap: break-word;
}

/* make images easier to work with */
img,
picture {
  max-width: 100%;
  display: block;
}

/* make form elements easier to work with */
input,
button,
textarea,
select {
  font: inherit;
}

/* Create a root stacking context */
#root,
#__next {
  isolation: isolate;
}

/* remove animations for people who've turned them off */
@media (prefers-reduced-motion: reduce) {
  *,
  *::before,
  *::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}
li {
  list-style: none;
}

:root {
  --clr-background-light-gray: #fafafa;
  --clr-white: #ffffff;
  --clr-dark-grayish-blue-400: #393a4b;
  --clr-dark-grayish-blue-200: #494c6b;
  --clr-light-gray-400: #9495a5;
  --clr-lighter-gray-200: #d1d2da;
  --clr-lightest-gray-100: #e3e4f1;
  --clr-light-blue: #3a7cfd;
  --clr-primary-gradient: linear-gradient(135deg, #55ddff 0%, #c058f3 100%);
  --clr-background-dark-gray: #171823;
  --clr-midnight-black: #25273d;
  --clr-dark-gray-text: #767992;
  --clr-light-gray-text: #4d5067;
  --clr-whitish-gray: #c8cbe7;
  --clr-dolphin-gray: #5b5e7e;
  --clr-blueish-white: #e3e4f1;
  --ff-josefin-sans: "Josefin Sans", sans-serif;
}

body {
  font-family: var(--ff-josefin-sans);
  font-weight: 400;
  color: var(--clr-dark-grayish-blue-400);
  background-color: var(--bg-body);
}

h1,
h2,
h3,
h4,
h5,
h6 {
  color: var(--text-header-primary);
  font-weight: 700;
}

button {
  cursor: pointer;
  background-color: transparent;
  border: none;
}

li {
  color: var(--text-list-item);
}

input {
  background-color: var(--bg-search-input);
  color: var(--text-search-input);
}

input::placeholder {
  color: var(--text-search-input-placeholder);
}

.tabs button {
  color: var(--text-tabs);
}

.tabs button:hover {
  color: var(--text-hover-tabs);
}

.text-wrapper {
  color: var(--drag-drop-text);
}

body#light {
  --bg-body: var(--clr-background-light-gray);
  --bg-list-item: var(--clr-white);
  --bg-search-input: var(--clr-white);
  --text-list-item: var(--clr-dark-grayish-blue);
  --text-header-primary: var(--clr-white);
  --text-search-input: var(--clr-dark-grayish-blue-400);
  --text-search-input-placeholder: var(--clr-light-gray-400);
  --bg-status-bar: var(--clr-white);
  --drag-drop-text: var(--clr-light-gray-400);
  --items-left-text: var(--clr-light-gray-400);
  --clear-completed-text: var(--clr-light-gray-400);
  --text-tabs: var(--clr-light-gray-400);
  --text-hover-tabs: var(--clr-dark-grayish-blue-200);
  --clear-completed-text-hover: var(--clr-dark-grayish-blue-200);
  --bg-image-mobile: url("/src/assets/images/bg-mobile-light.jpg");
  --bg-image-desktop: url("/src/assets/images/bg-desktop-light.jpg");
}

body#dark {
  --bg-body: var(--clr-background-dark-gray);
  --bg-list-item: var(--clr-midnight-black);
  --bg-search-input: var(--clr-midnight-black);
  --text-header-primary: var(--clr-white);
  --text-search-input: var(--clr-whitish-gray);
  --text-list-item: var(--clr-whitish-gray);
  --text-search-input-placeholder: var(--clr-dark-gray-text);
  --bg-status-bar: var(--clr-midnight-black);
  --drag-drop-text: var(--clr-dolphin-gray);
  --items-left-text: var(--clr-dolphin-gray);
  --clear-completed-text: var(--clr-dolphin-gray);
  --text-tabs: var(--clr-dolphin-gray);
  --text-hover-tabs: var(--clr-white);
  --clear-completed-text-hover: var(--clr-white);
  --bg-image-mobile: url("/src/assets/images/bg-mobile-dark.jpg");
  --bg-image-desktop: url("/src/assets/images/bg-desktop-dark.jpg");
}
body#dark .create-bar {
  box-shadow: 0px -10px 20px -12px rgba(0, 1, 14, 0.785);
}
body#dark .todo-list {
  box-shadow: 0px -10px 20px -12px rgba(0, 1, 14, 0.785);
}
body#dark .wrapper {
  box-shadow: 0px -10px 20px -12px rgba(0, 1, 14, 0.785);
}

.center {
  --measure: 540px;
  box-sizing: content-box;
  margin-inline: auto;
  max-inline-size: var(--measure);
}

.sr-only {
  position: absolute !important;
  width: 1px !important;
  height: 1px !important;
  padding: 0 !important;
  margin: -1px !important;
  overflow: hidden !important;
  clip: rect(0, 0, 0, 0) !important;
  white-space: nowrap !important;
  border: 0 !important;
}

.bg-primary-gradient {
  background-image: var(--clr-primary-gradient);
}

.text-active-state {
  color: var(--clr-light-blue);
}

.d-none {
  display: none;
}

header {
  background-image: var(--bg-image-mobile);
  background-size: cover;
  color: var(--clr-white);
  padding: 48px 26px;
  display: flex;
  flex-direction: column;
  gap: 40px;
}
header .todo-wrapper {
  display: flex;
  flex-wrap: wrap;
  align-items: baseline;
  justify-content: space-between;
  letter-spacing: 15px;
  width: 100%;
}
@media (min-width: 48rem) {
  header {
    background-image: var(--bg-image-desktop);
  }
}
header form {
  position: relative;
  width: 100%;
}
header form::before {
  position: absolute;
  content: "";
  height: 24px;
  width: 24px;
  border: 2px solid var(--clr-lightest-gray-100);
  border-radius: 50%;
  transform: translate(90%, 50%);
}
header .create-bar {
  padding: 14px;
  padding-left: 60px;
  border-radius: 5px;
  border: none;
  width: 100%;
}

main {
  display: flex;
  flex-direction: column-reverse;
  padding-inline: 20px;
  transform: translateY(-24px);
}
main::before {
  content: "Drag and drop to reorder list";
  color: var(--drag-drop-text);
  margin-block: 45px;
  text-align: center;
}

.todo-list {
  background-color: var(--bg-list-item);
  box-shadow: 0px -33px 50px -2px rgba(194, 195, 214, 0.5);
  border-radius: 5px;
  isolation: isolate;
}
.todo-list .list-item {
  z-index: 1;
  position: relative;
  display: flex;
  gap: 12px;
  align-items: center;
  padding-inline: 20px;
  padding-block: 16px;
  border-bottom: 1px solid black;
}
@media (min-width: 48rem) {
  .todo-list .list-item {
    padding-block: 23px;
  }
}
.todo-list .complete-btn {
  position: relative;
  flex-shrink: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px solid var(--clr-lightest-gray-100);
  height: 20px;
  width: 20px;
  border-radius: 50%;
  background-color: white;
}
.todo-list .complete-btn:hover {
  border: none;
}
.todo-list .complete-btn:hover::after {
  content: "";
  position: absolute;
  top: -1px;
  bottom: -1px;
  left: -1px;
  right: -1px;
  background: var(--clr-primary-gradient);
  z-index: -1;
  border-radius: 50%;
}
.todo-list .delete-btn {
  flex-shrink: 0;
}
@media (min-width: 48rem) {
  .todo-list .complete-btn {
    height: 24px;
    width: 24px;
  }
}
.todo-list .delete-btn {
  margin-left: auto;
}

.status-bar {
  display: grid;
  grid-template-columns: 1fr 1fr;
  justify-items: center;
  row-gap: 24px;
  background-color: transparent;
}
.status-bar .wrapper {
  grid-column: 1/-1;
  display: flex;
  justify-content: space-between;
  width: 100%;
  box-shadow: 0px 20px 50px -15px rgba(194, 195, 214, 0.5);
  border-radius: 5px;
  padding: 20px;
  background-color: var(--bg-status-bar);
}
.status-bar .items-left {
  color: var(--items-left-text);
}
.status-bar .clear-btn {
  color: var(--clear-completed-text);
}
.status-bar .clear-btn:hover {
  color: var(--clear-completed-text-hover);
}
.status-bar .tabs {
  display: flex;
  justify-content: center;
  grid-column: 1/-1;
  padding-block: 15px;
  gap: 19px;
  width: 100%;
  border-radius: 5px;
  font-weight: 700;
  background-color: var(--bg-status-bar);
}
.status-bar .alternate-status-bar {
  display: none;
}
@media (min-width: 48rem) {
  .status-bar {
    display: block;
  }
  .status-bar .wrapper,
  .status-bar .tabs {
    display: none;
  }
  .status-bar .alternate-status-bar {
    background-color: var(--bg-status-bar);
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    align-items: center;
    box-shadow: 0px -10px 20px -12px rgba(0, 1, 14, 0.785);
    padding-inline: 24px;
    padding-top: 16px;
    padding-bottom: 20px;
  }
  .status-bar .alternate-status-bar .tabs {
    display: flex;
  }
  .status-bar .alternate-status-bar .items-left {
    grid-column: 1/2;
    grid-row: 1/2;
    justify-self: start;
  }
  .status-bar .alternate-status-bar .clear-btn {
    grid-column: 3/4;
    justify-self: end;
  }
  .status-bar .alternate-status-bar .tabs {
    box-shadow: none;
    grid-column: 2/3;
    grid-row: 1/2;
    width: auto;
  }
}

footer {
  display: none;
}
footer .attribution {
  font-size: 11px;
  text-align: center;
}
footer .attribution a {
  color: hsl(228deg, 45%, 44%);
}

.list-item[data-completed=true] {
  color: var(--clr-lighter-gray-200);
  text-decoration: line-through;
}

.list-item[data-completed=true] .complete-btn {
  background-image: var(--clr-primary-gradient);
  border: none;
}

.list-item[data-completed=true] .complete-btn:hover::after {
  content: none;
}

.tabs .tab[aria-selected=true] {
  color: var(--clr-light-blue);
}